import React from 'react';
import './typeCash.css';

const TypeCash = ({ onTypeChange, searchChange, dollarTypeInvoice, dollarTypeIncome, cashOut }) => {


    return (
        <div className='pa2'>
            <h1>Type for cashOut</h1>
            <label className='yellow' htmlFor='inputText'>Income per character = $ {dollarTypeIncome}</label>
            <br></br>
            <textarea 
            id = 'inputText'
            className='pa3 ba b--green bg-lightest-blue'
            type='text' 
            maxLength="500"
            placeholder='write a book'
            onChange = {onTypeChange}
            />
            <br></br>
            <button className='pa1 f2 grow bw2 shadow-5' id='cashOut' onClick={cashOut}>Sell text for ${dollarTypeInvoice}</button>
        </div>
    );

}

export default TypeCash;