import React from 'react';

const Card = ({upgradeId, name, cost, effect, owned}) => {
    if (owned == false){
        return(
            <div  id = {'upgrade' + upgradeId} className='tc bg-light-green dib br3 ma2 grow bw2 shadow-5'>
                <img alt='roboimg' src={`https://robohash.org/${upgradeId}?200x200`}/>
                <div>
                    <h2>{name}</h2>
                    <p>{effect}</p>
                    <h2>Only ${cost}</h2>
                    
                </div>
            </div>
        );
    } else {
        return(
            <div  id = {'upgrade' + upgradeId} className='tc bg-light-yellow dib br3 ma2 grow bw2 shadow-5'>
                <img alt='roboimg' src={`https://robohash.org/${upgradeId}?200x200`}/>
                <div>
                    <h2>{name}</h2>
                    <p>{effect}</p>
                    <h2>OWNED</h2>
                    
                </div>
            </div>
        );

    };


}
export default Card;