import React from 'react';

const StatsBox = ({ dollarIncome, hoursPassed, daysPassed, yearsPassed}) => {


    return (
        <div className='pa2 light-gray'>
            <p>Your Current Income is ${dollarIncome} / hour</p>
            <p>{Math.round(yearsPassed)} years / {Math.round(daysPassed)} days / {hoursPassed} hours passed</p>

        </div>
    );
}

export default StatsBox;