import React from 'react';
import './clickCash.css';

const ClickCash = ({ clickIncome, clickCashOut }) => {


    return (
        <div className='pa2'>
            <h1>Click for Cash!</h1>
            
            <br></br>
            <label className='yellow'>Buck a Clicker</label>
            <br></br>
            <button className='pa4 f2 grow bw2 shadow-5'  onClick={clickCashOut}>${clickIncome}</button>
        </div>
    );

    // const cashOut = document.getElementById('cashOut');
    // cashOut.addEventListener('click', function (event) {
    //     alert('Element clicked through function!');
    //   });
}

export default ClickCash;