import React, { Component } from 'react';
import CardList from '../components/CardList.js';
import Reset from '../components/Reset.js';
import TypeCash from '../components/TypeCash.js';
import ClickCash from '../components/ClickCash.js';
import StatsBox from '../components/StatsBox.js';
// import UpgradeBox from '../components/UpgradeBox.js';
import MainCounter from '../components/MainCounter.js';
import Scroll from '../components/Scroll.js';
import { upgrades } from './upgrades.js';
import './App.css';
import SearchBox from '../components/SearchBox.js';


class App extends Component {
    constructor(){
        super()       
        this.state = {
            tickerInterval: 200,
            upgrades: upgrades,
            searchfield: '',
            mainCounter: 0,
            hoursPassed: 0,
            daysPassed: 0,
            yearsPassed: 0,
            dollarTypeIncome: 1,
            dollarTypeInvoice: 0,
            dollarIncome: 0,
            clickIncome: 1,
        }

        // Retrieving localSaveFile:
        let localSaveFile, activeSaveFile;
        localSaveFile = localStorage.getItem("localSaveFile");
        activeSaveFile = JSON.parse(localSaveFile);
        // load save into this.state
        this.state.mainCounter = activeSaveFile.mainCounter;
        this.state.hoursPassed = activeSaveFile.hoursPassed;
        this.state.daysPassed = activeSaveFile.daysPassed;
        this.state.yearsPassed = activeSaveFile.yearsPassed;
        this.state.dollarTypeIncome = activeSaveFile.dollarTypeIncome;
        this.state.dollarTypeInvoice = activeSaveFile.dollarTypeInvoice;
        this.state.dollarIncome = activeSaveFile.dollarIncome;
        this.state.clickIncome = activeSaveFile.clickIncome;   

        // Retrieving localUpgrades:
        let localUpgrades = localStorage.getItem("localUpgrades");
        let activeUpgrades = JSON.parse(localUpgrades);
        
        for (const [key] of Object.entries(activeUpgrades)) {
            let x = activeUpgrades[key].value.owned;
            console.log('activeUpgrades[key].value.owned', x);
            this.state.upgrades[key].owned =  x ;
        }
    }



    componentDidMount() {  


        // Applys Upgrade
        const clickUpgrade = (event, idUpgrade, upgradeCost, upgradeDollarIncome, upgradeClickIncome, dollarTypeIncome) => {
            let currentUpgrade = "upgrade"+idUpgrade;
            console.log('click');
            if (this.state.mainCounter >= upgradeCost && this.state.upgrades[idUpgrade].owned === false){
                console.log('can afford');
                // buy item
                this.setState((state) => ({ dollarIncome: state.dollarIncome + upgradeDollarIncome}));
                this.setState((state) => ({ clickIncome: state.clickIncome + upgradeClickIncome}));
                this.setState((state) => ({ mainCounter: state.mainCounter - upgradeCost}));
                this.setState((state) => ({ dollarTypeIncome: state.dollarTypeIncome + dollarTypeIncome}));
                

                // set bought item to owned
                const buyUpgrades = this.state.upgrades;
                buyUpgrades[idUpgrade].owned = true;
                this.setState((state) => ({ upgrades: buyUpgrades}));

                // owned
                document.getElementById(currentUpgrade).childNodes[1].childNodes[0].innerText= 'Owned';
            } else if (this.state.mainCounter <= upgradeCost) {
                // cant afford
            }     
        }
        
        // Main Ticker
        setInterval(() => {
            const { hoursPassed, daysPassed } = this.state;
            if (hoursPassed < 24){
                this.setState((state) => ({ hoursPassed: state.hoursPassed + 1}));
            }
            else {
                this.setState((state) => ({ daysPassed: state.daysPassed + 1}));
                this.setState((state) => ({ hoursPassed: 0}));
                if (daysPassed === 365){
                    this.setState((state) => ({ daysPassed: 0}));
                    this.setState((state) => ({ yearsPassed: state.yearsPassed + 1}));
                }
            }  
            this.setState((state) => ({ mainCounter: state.mainCounter + state.dollarIncome}));
            this.saveGame();
        }, this.state.tickerInterval);



        // Loop thru upgrade list
        for (const [key] of Object.entries(this.state.upgrades)) {
            let upgradeID = 'upgrade' + this.state.upgrades[key].id;
            let upgradeCost = this.state.upgrades[key].cost;
            let upgradeDollarIncome = this.state.upgrades[key].dollarIncome;
            let upgradeClickIncome = this.state.upgrades[key].clickIncome;
            let upgradeTypeIncome = this.state.upgrades[key].dollarTypeIncome;
            console.log(upgradeID, upgradeDollarIncome, upgradeClickIncome, upgradeCost, upgradeTypeIncome);
            // add click listener to each card 

            document.getElementById(upgradeID).classList.add("bg-light-green");
            document.getElementById(upgradeID).classList.remove("bg-light-yellow");              
            document.getElementById(upgradeID).addEventListener("click", function(event, arg1, arg2, arg3, arg4, arg5){
                clickUpgrade(event, key, upgradeCost, upgradeDollarIncome, upgradeClickIncome, upgradeTypeIncome); 
            }, false);

            // check if cards are owned
            if (this.state.upgrades[key].owned === true){                
                // document.getElementById(upgradeID).childNodes[1].childNodes[0].innerText= 'Owned';
                document.getElementById(upgradeID).classList.remove("bg-light-green");
                document.getElementById(upgradeID).classList.add("bg-light-yellow");
            }

        }
    }   




    // add $ for each char typed
    onTypeChange = (event) => {
        this.setState((state) => ({ dollarTypeInvoice: state.dollarTypeInvoice + state.dollarTypeIncome }));
    }
    // Filter
    onSearchChange = (event) => {
        this.setState({searchfield: event.target.value})
    }

    // SAVE to local storage
    saveGame = () => { 
        // Storing data:
        let localSaveObj;
        localSaveObj = {
            hoursPassed: this.state.hoursPassed,
            daysPassed: this.state.daysPassed,
            yearsPassed: this.state.yearsPassed,
            mainCounter: this.state.mainCounter,
            dollarTypeIncome: this.state.dollarTypeIncome,
            dollarTypeInvoice: this.state.dollarTypeInvoice,
            dollarIncome: this.state.dollarIncome,
            clickIncome: this.state.clickIncome,
        };
        let localSaveObjJSON = JSON.stringify(localSaveObj);
        localStorage.setItem("localSaveFile", localSaveObjJSON);



        // Save upgrades Obj to Local Storage
        let localUpgrades;
        localUpgrades = {};

        for (const [key, value] of Object.entries(this.state.upgrades)) {
            localUpgrades[key] = { value,};
        }
        let localUpgradesJSON = JSON.stringify(localUpgrades);
        localStorage.setItem("localUpgrades", localUpgradesJSON);
    }

    // PAY into Total
    payout = (incomeSource) => {
        console.log(incomeSource);
        this.setState((state) => ({ mainCounter: state.mainCounter + incomeSource}));
    }

    // payout textbox cash
    cashOut = () =>{
        this.payout(this.state.dollarTypeInvoice);
        this.setState((state) => ({ dollarTypeInvoice: 0}));
        document.getElementById("inputText").value = "Type for money";
    }
    // payout click cash
    clickCashOut = () => {
        this.payout(this.state.clickIncome);
    }
    // Reset game
    resetButton = () => {
        console.log('reset');
        this.setState((state) => ({ mainCounter: 0}));
        this.setState((state) => ({ hoursPassed: 0}));
        this.setState((state) => ({ daysPassed: 0}));
        this.setState((state) => ({ yearsPassed: 0}));
        this.setState((state) => ({ dollarTypeIncome: 1}));
        this.setState((state) => ({ dollarTypeInvoice: 0}));
        this.setState((state) => ({ dollarIncome: 0}));
        this.setState((state) => ({ clickIncome: 1}));
        
        // reset upgrades
        for (const [key] of Object.entries(this.state.upgrades)) {
            const noUpgrades = this.state.upgrades;
            noUpgrades[key].owned = false;         
            this.setState((state) => ({ upgrades: noUpgrades}));     
        }

        // reset color of cards
        for (const [key] of Object.entries(this.state.upgrades)) {
            let upgradeID = 'upgrade' + this.state.upgrades[key].id;
            document.getElementById(upgradeID).classList.add("bg-light-green");
            document.getElementById(upgradeID).classList.remove("bg-light-yellow");   
        }
        // window.location.reload();

    }
    


    render()   {
        const { upgrades, searchfield, mainCounter, dollarIncome, clickIncome, hoursPassed, daysPassed, yearsPassed, dollarTypeInvoice,dollarTypeIncome } = this.state;
        const filteredUpgrades = upgrades.filter(robot => {
            return robot.name.toLowerCase().includes(searchfield.toLowerCase());
        })
        // if no data is in robots display loading screen
        return(
                <div className='tc'>
                    <h1 className='f1'>Billionaire!</h1>
                    <MainCounter mainCounter = {mainCounter} />
                    <StatsBox 
                    dollarIncome = {dollarIncome} 
                    hoursPassed = {hoursPassed} 
                    daysPassed = {daysPassed} 
                    yearsPassed = {yearsPassed} 
                    />
                    <Scroll>
                    <TypeCash 
                    onTypeChange = {this.onTypeChange}
                    dollarTypeInvoice ={dollarTypeInvoice}
                    dollarTypeIncome ={dollarTypeIncome}
                    cashOut = {this.cashOut}
                    />
                    <ClickCash
                    clickIncome = {clickIncome}
                    clickCashOut = {this.clickCashOut}
                    />
                    {/* <UpgradeBox/> */}
                    <SearchBox searchChange = {this.onSearchChange} />
                    <CardList upgrades={filteredUpgrades} />
                    </Scroll>
                    <Reset resetButton = {this.resetButton}/>
                </div>     
            ); 
        }
    }





export default App; 

